/**
 * Give - Stripe Gateway Add-on ADMIN JS
 */
jQuery.noConflict();
(function ($) {

	//On DOM Ready
	$(function () {

		// Clicking on the Update button
		$('.give-stripe-customer-id-update').on('click', function (e) {
			e.preventDefault();
			$('.give-stripe-customer-link').hide();
			$(this).hide();
			$('.give-stripe-customer-submit-wrap, .give-stripe-customer-id-input').show();
		});

		// Cancel updating.
		$('.give-stripe-customer-id-cancel').on('click', function (e) {
			e.preventDefault();
			$('.give-stripe-customer-link, .give-stripe-customer-id-update').show();
			$('.give-stripe-customer-submit-wrap, .give-stripe-customer-id-input').hide();
		});

	});

})(jQuery);


